<script setup>
import { h } from 'vue'
import { ElNotification } from 'element-plus'
import { onMounted, ref, computed } from 'vue'
import { StarFilled } from '@element-plus/icons-vue'
const open1 = () => {
    ElNotification({
        title: 'Hello~',
        message: h('i', { style: 'color: teal' }, '欢迎进入'),
    })
}
onMounted(() => {
    open1();
});

const futureDateString = '2024-08-23';
const futureDate = ref(new Date(futureDateString)); // 创建一个响应式的futureDate

// 计算属性：计算未来日期与今天之间的天数差（确保未来日期在今天之后）
const daysUntilFutureDate = computed(() => {
    const today = new Date();
    const oneDayMs = 24 * 60 * 60 * 1000; // 一天的毫秒数
    const diffInMs = futureDate.value.getTime() - today.getTime();
    if (diffInMs > 0) {
        return Math.ceil(diffInMs / oneDayMs); // 如果未来日期在今天之后，向上取整得到天数
    } else {
        return 0; // 如果未来日期在今天之前或等于今天，则显示为0天
    }
});
</script>

<template>
    <div id="app">
        <div id="centered-content">
            <p>距离 注册会计师考试({{ futureDateString }}) 还有 {{ daysUntilFutureDate }} 天</p>
            <el-divider>
                <el-icon><star-filled /></el-icon>
            </el-divider>
            <el-link href="https://cpaexam.cicpa.org.cn/">注册会计师全国统一考试网上报名入口</el-link>
            <el-link
                href="https://cpaexam.cicpa.org.cn/files/articlehtml/statichtml/01/0000018BFF19B498C0B03256BD2BB543.html">考试报名时间和考试时间入口</el-link>
        </div>
    </div>
</template>

<style scoped>
.el-link {
    margin-right: 8px;
}

#app {
    display: flex;
    justify-content: center;
    /* 水平居中 */
    align-items: center;
    /* 垂直居中 */
    height: 100vh;
    /* 设置整个视窗高度 */
    margin: 0;
    /* 清除默认的页边距 */
}

.centered-content {
    /* 其他样式，如宽度、高度等 */
}</style>

